@import '~bpk-mixins';

@mixin container {
  max-width: $bpk-breakpoint-desktop;
  margin: 0 auto;
}

body {
  background-color: #5abe57;
}

.App {
  text-align: center;

  &__header {
    padding: $bpk-spacing-lg * 2;
    color: #fff;

    @include bpk-breakpoint-mobile {
      padding: $bpk-spacing-xl;
    }
  }

  &__main {
    padding: $bpk-spacing-xxl;

    @include bpk-breakpoint-mobile {
      padding: $bpk-spacing-base;
    }
  }

}
