@import '~bpk-mixins';

.AllIcons {
  background-color: #eee;
  border: 15px solid #fff;
  box-shadow: 0 0 10px rgba(#000, 0.1);
  margin: 20px auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 20px;
  justify-content: space-evenly;
  align-content: space-evenly;
  border-radius: 25px;
  padding: 20px;

  &__icon {
    width: 60px;
    height: 60px;
  }
}
